import Vue from 'vue'
import Router from 'vue-router'
import MHomePage from '@/pages/MHomePage'
import HomePage from '@/pages/HomePage'
import UserInfoUpload from '@/pages/UserInfoUpload'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HomePage',
      component: HomePage
    },
    {
      
      path: '/phonepage',
      name: 'MHomePage',
      component: MHomePage
    },
    {
      path: '/userinfo_upload',
      name: 'UserInfoUpload',
      component: UserInfoUpload
    }
  ]
})



