                <div class="question_card">
                    <div class="question">1.您的年龄</div>
                    <div class="answers">
                        <div class="answer">
                            <radio v-model="age" :options="ageOptions"/>
                        </div>
                    </div>
                </div>
                <div class="question_card">
                    <div class="question">2.您的学历</div>
                    <div class="answers">
                        <div class="answer">
                            <radio v-model="education" :options="educationOptions"/>
                        </div>
                    </div>
                </div>
                <div class="question_card">
                    <div class="question">3.您的职业</div>
                    <div class="answers">
                        <div class="answer">
                            <radio v-model="occupation" :options="occupationOptions"/>
                        </div>
                    </div>
                </div>
                <div class="question_card">
                    <div class="question">4.您所在的行业</div>
                    <div class="answers">
                        <div class="answer">
                            <radio v-model="industry" :options="industryOptions"/>
                        </div>
                    </div>
                </div>
                <div class="question_card">
                    <div class="question">5.您所在的城市是</div>
                    <div class="answers">
                        <input v-model="city" type="text" class="text_input" placeholder="填写城市">
                    </div>
                </div>
                <div class="question_card">
                    <div class="question">6.您是否已有海外银行账户（包括香港账户）</div>
                    <div class="answers">
                        <div class="answer">
                            <radio v-model="account" :options="accountOptions"/>
                        </div>
                    </div>
                </div>
                <div class="question_card">
                    <div class="question">7.您有过以下哪些海外投资经验？[多选题]</div>
                    <div class="answers">
                        <div class="answer">
                            <checklist v-model="investmentExperience" :options="investmentExperienceOptions"/>
                        </div>
                    </div>
                </div>
                <div class="question_card">
                    <div class="question">8.2018年，您预计可用来做海外投资的资金额度是？</div>
                    <div class="answers">
                        <div class="answer">
                            <radio v-model="fundsAmount" :options="fundsAmountOptions"/>
                        </div>
                    </div>
                </div>
                <div class="question_card">
                    <div class="question">9.您最关心WeHome海外房产合投项目的哪方面信息？[多选题]</div>
                    <div class="answers">
                        <div class="answer">
                            <checklist v-model="aspectInfo" :options="aspectInfoOptions"/>
                        </div>
                    </div>
                </div>
                <div class="question_card">
                    <div class="question">10.如果您得知，wehome美国房产合投香项目的年化收益是10%以上，起投金额5000美金，您是否愿意参与，作为一种新的美元投资方式？</div>
                    <div class="answers">
                        <div class="answer">
                            <radio v-model="isJoin" :options="isJoinOptions"/>
                        </div>
                    </div>
                </div>
                <div class="info">感谢您的耐心作答！我们会根据您的回答，对您进行简单了解，以评估您的需求和投资匹配度。如有任何疑问，欢迎点击客服标志，加客服微信咨询。我们的工作人员会议电话或微信方式和您取得联系，请保持通讯畅通，再次感谢您的信任与关注！</div>
                <input v-model="name" type="text" class="text_input" placeholder="填写姓名 *">
                <input v-model="mobile" type="tel" class="text_input" placeholder="填写手机号码 *">
                <input v-model="email" type="text" class="text_input" placeholder="填写电子邮箱 *">
                <div class="submit" @click="submit">完成</div>
            </div>
        </div>
    </div>
</template>

<script>
    import PageHeader from '../components/PageHeader'
    import { Radio,Checklist  } from 'mint-ui';
    export default {
        name: 'UserInfoUpload',
        data() {
            return {
                isComputer: true,
                age: '',
                ageOptions: [{
                    label: '20岁及以下',
                    value: 'A',
                }, {
                    label: '21-25岁',
                    value: 'B',
                }, {
                    label: '25-30岁',
                    value: 'C',
                }, {
                    label: '31-40岁',
                    value: 'D',
                }, {
                    label: '40岁及以上',
                    value: 'E',
                }],
                education: '',
                educationOptions: [{
                    label: '高中及以下',
                    value: 'A',
                }, {
                    label: '大专或职业学校',
                    value: 'B',
                }, {
                    label: '本科',
                    value: 'C',
                }, {
                    label: '硕士及以上',
                    value: 'D',
                }, {
                    label: '海外本科及以上',
                    value: 'E',
                }],
                occupation: '',
                occupationOptions: [{
                    label: '公司白领',
                    value: 'A',
                }, {
                    label: '公司中高层管理者',
                    value: 'B',
                }, {
                    label: '政府/机关/公务员',
                    value: 'C',
                }, {
                    label: '专业人员（如工程师/医生/老师等）',
                    value: 'D',
                }, {
                    label: '企业主',
                    value: 'E',
                }, {
                    label: '自由职业者',
                    value: 'F',
                }, {
                    label: '退休',
                    value: 'G',
                }, {
                    label: '其他',
                    value: 'H',
                }],
                industry: '',
                industryOptions: [{
                    label: '互联网/信息技术',
                    value: 'A',
                }, {
                    label: '金融业',
                    value: 'B',
                }, {
                    label: '房地产行业',
                    value: 'C',
                }, {
                    label: '制造业/零售业/服务业',
                    value: 'D',
                }, {
                    label: '科研/教育机构',
                    value: 'E',
                }, {
                    label: '政府机关',
                    value: 'F',
                },  {
                    label: '其他',
                    value: 'G',
                }],
                city: '',
                account: '',
                accountOptions: [{
                    label: '有',
                    value: 'A',
                }, {
                    label: '没有，但如有好的美金理财方式，可以办一个',
                    value: 'B',
                }, {
                    label: '没有，暂时不打算办',
                    value: 'C',
                }],
                investmentExperience: [],
                investmentExperienceOptions: [{
                    label: '有美元储蓄、美元基金、债券固收类投资',
                    value: 'A',
                }, {
                    label: '投资过港股或美股',
                    value: 'B',
                }, {
                    label: '投资过海外房产',
                    value: 'C',
                }, {
                    label: '投资Retis（房产基金）、海外房产众筹类金融衍生品',
                    value: 'D',
                }, {
                    label: '没有海外投资经验',
                    value: 'E',
                }],
                fundsAmount: '',
                fundsAmountOptions: [{
                    label: '5千美金以下',
                    value: 'A',
                }, {
                    label: '5千-1万美金',
                    value: 'B',
                }, {
                    label: '1万-5万美金',
                    value: 'C',
                }, {
                    label: '5万-10万美金',
                    value: 'D',
                }, {
                    label: '10万美金以上',
                    value: 'E',
                }],
                aspectInfo: [],
                aspectInfoOptions: [{
                    label: '如何设立海外账户',
                    value: 'A',
                }, {
                    label: '如何获得分红收益',
                    value: 'B',
                }, {
                    label: '回报率是否高于市场平均水平',
                    value: 'C',
                }, {
                    label: '投资存在哪些潜在风险',
                    value: 'D',
                }, {
                    label: '如何退出此项目',
                    value: 'E',
                }, {
                    label: '投资过程中，都会产生哪些费用',
                    value: 'F',
                }, {
                    label: '其他',
                    value: 'G',
                }],
                isJoin: '',
                isJoinOptions: [{
                    label: '是，需要了解详情，很愿意参与',
                    value: 'A',
                }, {
                    label: '不确定，有不少顾虑，希望有专业人士答疑解惑',
                    value: 'B',
                }, {
                    label: '否，不会考虑参与',
                    value: 'C',
                }],
                name: '',
                mobile: '',
                email: ''
            }
        },
        mounted() {
            this.isComputer = this.isPc();
        },
        methods: {
            submit() {
                if (!this.name) {
                    this.showToast('请输入用户名');
                    return;
                }
                if (!this.mobile) {
                    this.showToast('请输入手机号');
                    return;
                }
                if (!this.email) {
                    this.showToast('请输入email');
                    return;
                }
                this.getResponse('/question/answer', {
                    mobild: this.mobile,
                    name: this.name,
                    email: this.email,
                    answer: {
                        1: [this.age],
                        2: [this.education],
                        3: [this.occupation],
                        4: [this.industry],
                        5: [this.city],
                        6: [this.account],
                        7: this.investmentExperience,
                        8: [this.fundsAmount],
                        9: this.aspectInfo,
                        10: [this.isJoin]
                    }
                }).then(res => {
                    if (res.code === 0) {
                        this.showToast('提交成功！');
                    } else {
                        this.showToast(res.msg || '接口调用失败');
                    }
                }).catch(e => {
                    console.log(e.message);
                    this.showToast('接口调用失败');
                })
            }
        },
        components: {
            PageHeader,
            Radio,
            Checklist
        }
    }
</script>

<style lang="scss">
.userinfo_upload_pc{
    .content{
        width: 100%;
        background: url('../assets/info_bg.jpg') no-repeat;
        background-size: cover;
        background-position: center center;
        position: relative;
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 20px 20%;
        box-sizing: border-box;
        overflow: hidden;
        .table{
            background: #fff;
            padding: 50px 70px;
            border-radius: 5px;
            .title{
                font-size: 20px;
                font-weight: 400;
                margin-bottom: 40px;
            }
            .question{
                margin: 20px 0;
            }
            .text_input{
                width: 100%;
                height: 40px;
                line-height: 40px;
                padding-left: 5px;
                box-sizing: border-box;
                border-radius: 3px;
                outline: none;
                appearance: none;
                border: 1px #ccc solid;
                margin: 20px 0;
            }
            .mint-radiolist, .mint-checklist{
                width: 100%;
                display: flex;
                flex-wrap: wrap;
            }
            .mint-radiolist-title, .mint-checklist-title{
                display: none;
            }
            .mint-checkbox-label, .mint-radiolist-label{
                line-height: 30px;
            }
            .mint-cell{
                width: 50%;
            }
            .mint-cell:last-child{
                background: none;
            }
            .info{
                margin: 20px 0;
            }
            .submit{
                width: 100%;
                height: 50px;
                line-height: 50px;
                background: rgb(232, 67, 59);
                color: #fff;
                font-size: 20px;
                text-align: center;
                border-radius: 5px;
            }
        }
    }
}
.userinfo_upload_phone{
    .content{
        width: 100%;
        background: url('../assets/info_bg.jpg') no-repeat;
        background-size: cover;
        background-position: center center;
        position: relative;
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 2rem 1.6rem;
        box-sizing: border-box;
        overflow: hidden;
        .table{
            background: #fff;
            padding: 2rem 1rem;
            border-radius: 5px;
            .title{
                color: #333333;
                font-size: 3.5rem;
                font-weight: 400;
                margin-bottom: 3rem;
            }
            .subtitle{
                color: #666666;
            }
            .question{
                margin: 20px 0;
                color: #666666;
            }
            .answer{
                color: #666666;
            }
            .text_input{
                width: 100%;
                height: 40px;
                line-height: 40px;
                padding-left: 5px;
                box-sizing: border-box;
                border-radius: 3px;
                outline: none;
                appearance: none;
                border: 1px #ccc solid;
                margin: 20px 0;
            }
            .mint-radiolist-title, .mint-checklist-title{
                display: none;
            }
            .mint-checkbox-label, .mint-radiolist-label{
                line-height: 3rem;
            }
            .mint-cell:last-child{
                background: none;
            }
            .info{
                margin: 20px 0;
                color: #666666;
            }
            .submit{
                width: 100%;
                height: 50px;
                line-height: 50px;
                background: rgb(232, 67, 59);
                color: #fff;
                font-size: 20px;
                text-align: center;
                border-radius: 5px;
            }
        }
    }
}
</style>



