<template>
    <header :class="isPc() ? 'page_header_pc' : 'page_header_phone'">
        <img src="../assets/logo.png" class="logo">
        <ul>
            <li>选择我们</li>
            <li>投资项目</li>
            <li>收益表现</li>
            <li>其他资源</li>
        </ul>
        <div class="register_block">
            <div class="sign_in">登录</div>
            <div class="register">注册</div>
        </div>
        <img src="../assets/menu.png" class="menu" @click="openMenu">
        <div class="mask" v-if="showMenu">
            <div class="right_menu">
                <div class="header">
                    <img src="../assets/dismiss_icon.png" class="close" @click="closeMenu">
                </div>
                <div class="blocks">
                    <div class="block">选择我们</div>
                    <div class="block">投资项目</div>
                    <div class="block">收益表现</div>
                    <div class="block">其他资源</div>
                    <div class="apply" @click="toApply">提交个人信息</div>
                </div>
            </div>
        </div>
    </header>
</template>

<script>
    export default {
        name: 'PageHeader',
        data() {
            return {
                isComputer: true,
                showMenu: false
            }
        },
        mounted() {
            this.isComputer = this.isPc();
        },
        methods: {
            openMenu() {
                const ele = document.getElementsByTagName('body')[0]; 
                ele.classList.add('forbidden_scroll');
                this.showMenu = true;
            },
            closeMenu() {
                const ele = document.getElementsByTagName('body')[0]; 
                ele.classList.remove('forbidden_scroll');
                this.showMenu = false;
            },
            toApply() {
                this.$router.push('/userinfo_upload');   
            }
        }
    }
</script>

<style lang="scss" scoped>
.page_header_pc{
    width: 100%;
    height: 64px;
    box-sizing: border-box;
    padding: 0 100px;
    .menu{
        display: none;
    }
    .logo{
        height: 40px;
        margin-top: 12px;
        float: left;
    }
    ul{
        padding: 0;
        margin: 0;
        list-style: none;
        display: inline-block;
        height: 64px;
        line-height: 64px;
        margin-left: 80px;
        li{
            float: left;
            font-size: 16px;
            color: #666666;
            height: 64px;
            cursor: pointer;
            &:hover{
                color: #333333;
                border-bottom: 4px #E8433B solid;
                box-sizing: border-box;
                height: 64px;
            }
            &:not(:first-child){
                margin-left: 40px;
            }
        }
    }
    .register_block{
        float: right;
        display: flex;
        align-items: center;
        height: 64px;
        font-size: 16px;
        .sign_in{
            border: 1px #C7CCD1 solid;
            border-radius: 5px;
            padding: 5px 15px;
            color: #666666;
            cursor: pointer;
        }
        .register{
            background: #E8433B;
            color: #fff;
            border-radius: 5px;
            padding: 5px 25px;
            margin-left: 20px;
            cursor: pointer;
        }
    }
}
.page_header_phone{
    width: 100%;
    height: 5rem;
    box-sizing: border-box;
    padding: 0 1.6rem;
    position: relative;
    .logo{
        height: 3.5rem;
        margin-top: 1rem;
        float: left;
    }
    .menu{
        height: 1.8rem;
        margin-top: 1.6rem;
        float: right;
    }
    .mask{
        position: fixed;
        top: 0;
        left: 0;
        height: 100vh;
        width: 100vw;
        background: rgba($color: #000000, $alpha: 0.3);
        z-index: 999;
        .right_menu{
            position: absolute;
            top: 0;
            right: 0;
            width: 70vw;
            height: 100vh;
            background: #fff;
            z-index: 1000;
            .header{
                height: 4rem;
                border-bottom: 0.1rem #ccc solid;
            }
            .close{
                height: 1.6rem;
                float: right;
                margin: 1.2rem 1rem;
            }
            .blocks{
                display: flex;
                justify-content: center;
                flex-wrap: wrap;
                .block{
                    width: 100%;
                    padding: 1rem 0 1rem 1.2rem;
                    box-sizing: border-box;
                }
                .apply{
                    background: rgb(232, 67, 53);
                    color: #fff;
                    height: 4rem;
                    line-height: 4rem;
                    text-align: center;
                    border-radius: 0.5rem;
                    width: 80%;
                    margin-top: 1.5rem;
                }
            }
        }
    }
    ul{
        display: none;
    }
    .register_block{
        display: none
    }
}
</style>



