<template>
    <div class="hourse_card" :style="style">
        <div class="preview">
            <img :src="data.headImg">
        </div>
        <div class="title">{{data.title}}</div>
        <div class="info">
            <div class="block">
                <div class="key">资产估值</div>
                <div class="val">{{data.asset}}</div>
            </div>
            <div class="block">
                <div class="key">分红收益</div>
                <div class="val">{{data.shareOutBonus}}</div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        props: {
            data: {
                type: Object
            }
        },
        data() {
            return {
                style: 'width: 30%'
            }
        },
        mounted() {
            this.style = this.isPc() ?  'width: 30%' : 'width: 100%';
        },
    }
</script>

<style lang="scss" scoped>
.hourse_card{
    overflow: hidden;
    border-radius: 10px;
    background: #fff;
    padding-bottom: 20px;
    cursor: pointer;
    &:hover{
        box-shadow: 0 10px 30px 10px rgba(36, 38, 40, 0.1);
    }
    .preview{
        width: 100%;
        height: 250px;
        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }
    .title{
        height: 50px;
        line-height: 50px;
        text-align: center;
        color: #333333;
        font-size: 16px;
        font-weight: 600;
    }
    .info{
        display: flex;
        .block{
            flex: 1;
            text-align: center;
            .key{
                color: #999999;
                font-size: 12px;
                margin: 5px 0;
            }
            .val{
                color: #666666;
                font-size: 18px;
            }
        }
    }
}
</style>



