import Vue from 'vue'
import App from './App'
import router from './router'
import axios from './http'

import MintUI from 'mint-ui'
import 'mint-ui/lib/style.css'

const Toast = MintUI.Toast;
const Indicator = MintUI.Indicator;

const isPc = function() {
    let userAgent = navigator.userAgent;
    if (/(iPhone|iPad|iPod|iOS|Android)/i.test(userAgent)) { //移动端
        return false;
    }
    return true;
};

const showToast = (message, duration = 5000) => {
    Toast({
        message: message,
        position: 'bottom',
        duration: duration
    });
};

Vue.use(MintUI);

Vue.config.productionTip = false;

Vue.prototype.isPc = isPc;

// 弱提示
Vue.prototype.showToast = showToast;

// 接口调用
Vue.prototype.getResponse = (url, params) => {
    if (!isPc()) {
        Indicator.open('加载中...');
    }
    return new Promise( (resolve, reject) => {
        axios.post(url, params).then( res => {
            if (!isPc()) {
                Indicator.close();
            }
            resolve(res);
        })
        .catch(e => {
            if (!isPc()) {
                Indicator.close();
            }
            console.log(e.message);
            resolve(e);
        })
    })
};

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



