import axios from 'axios'

const mode = process.env.NODE_ENV === 'production' ? 1 : 1; //0:本地 1:测试;

// axios 配置
axios.defaults.timeout = 10000;
if (mode === 0) {
    axios.defaults.baseURL = 'https://mock.lanrenyun.cn';
} else if (mode === 1) {
    axios.defaults.baseURL = 'http://sqm.leanapp.cn';
} else {
    axios.defaults.baseURL = 'http://sqm.leanapp.cn';
}

// http request 拦截器
axios.interceptors.request.use(
    config => {
        return config;
    },
    err => {
        return Promise.reject(err);
    });

// http response 拦截器
axios.interceptors.response.use(
    response => {
        return response.data;
    },
    error => {
        return Promise.reject(error.message)
    });

export default axios;



